
import { usePostStore } from "@/store/post";
import { defineComponent } from "vue";

export default defineComponent({
  props: {
    i: {
      type: Number,
      required: true,
    },
  },
  setup() {
    const posts = usePostStore();
    const post = posts.post;

    const loadmodal = (data: number) => {
      posts.modalcontroll();
      posts.postnum = data;
    };
    return { posts, post, loadmodal };
  },
});
