
import { usePostStore } from "@/store/post";
import { defineComponent } from "vue";

export default defineComponent({
  setup() {
    const posts = usePostStore();
    const modalclose = () => {
      posts.modalcontroll();
    };
    const deletecall = () => {
      const delconfirm = confirm("정말 삭제하시겠습니까?");
      if (delconfirm) {
        posts.deletepost(posts.postnum);
        alert("삭제되었습니다.");
        posts.modalcontroll();
      } else {
        alert("삭제가 취소되었습니다");
        posts.modalcontroll();
      }
    };
    return {
      modalclose,
      deletecall,
    };
  },
});
